import React from 'react'
import { HashRouter, Route, Routes } from 'react-router-dom'

import './App.css'
import { ManagerPage, ProjectPage } from './pages'

/**
 *   
 *        
 *        
 */
function App() {
  return (
      <HashRouter>
        <Routes>
          <Route path='/' element={<ManagerPage />} />
          <Route path='project/:id' element={<ProjectPage />} />
        </Routes>
      </HashRouter>
  )
}

export default App